import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import mindmap from '@/components/mindmap'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mindmap',
      component: mindmap
    }
  ]
})



